<script>
import props-menu from "@/views/props-menu.vue";
import { mapGetters } from "vuex";
import event-bus from "@/main";

export default {
  name: "phase-viewer",
  components: {
    props-menu
  },
  data: function() {
    return {
      isOpen: false,
      openType: ""
    };
  },
  mounted() {
    event-bus.$on("widgetBarMoved", e => {
      let drawer = document.getElementById("phase-viewer");
      drawer.style.top = e.pageY - 120 + "px";
    });
    event-bus.$on("typeSelected", type => (this.openType = type));
    event-bus.$on("typeDeselected", () => (this.openType = ""));
  },
  computed: {
    ...mapGetters([
      "currentWidget",
      "possibleSources",
      "selectedWidgetTypes",
      "phaseTitle",
      "submitTitle"
    ]),
    ...mapGetters("factory", ["getWidgets", "textSizes"]),
    wid() {
      return this.currentWidget;
    },
    size() {
      return this.$store.getters.size(this.wid);
    },
    answers() {
      return this.$store.getters.answers(this.wid);
    },
    optional() {
      return this.$store.getters.optional(this.wid);
    },
    possibleSources() {
      return this.$store.getters.possibleSources;
    },
    src() {
      return this.$store.getters.src(this.wid);
    },
    options() {
      return this.$store.getters.options(this.wid);
    },
    sources() {
      return this.$store.getters.sources(this.wid);
    }
  },
  methods: {
    toggleTab() {
      this.isOpen = !this.isOpen;
    },
    setPhaseTitle(e) {
      this.$store.commit("setPhaseTitle", e.target.value);
    },
    setSubmitTitle(e) {
      this.$store.commit("setSubmitTitle", e.target.value);
    },
    setProp(prop, value) {
      if (!this.wid) return;
      this.$store.commit("setProp", {
        prop: prop,
        value: value
      });
    },
    selectSources(select, prop) {
      var sources = [...select.options]
        .filter(option => option.selected)
        .map(option => parseInt(option.value, 10));
      this.setProp(prop, sources);
    },
    isSelected(sid) {
      if (!this.wid) return;
      return this.sources.includes(sid);
    },
    isSelectedWidget(type) {
      let value = false;
      this.selectedWidgetTypes.forEach(name => {
        if (name === type) {
          value = true;
          return;
        }
      });
      return value;
    },
    select-answers(e) {
      let selected = [];
      let collection = e.originalTarget.selectedOptions;
      for (let i = 0; i < collection.length; i++)
        selected.push(collection[i].label);
      this.$store.commit("setSelectedWidgetTypes", selected);
    },
    formatSource: src => `${src.wid}:${src.type}:${src.phase}`
  }
};
</script>

<template>
  <div id="phase-viewer" :class="['phase-viewer', { opened: isOpen }]">
    <div id="propertyPanel">
      <props-menu title="Phase" :openType="openType">
        <label for="Phase_PhaseTitle">
          Phase Title:
          <br />
          <input
            id="Phase_PhaseTitle"
            type="text"
            :value="phaseTitle"
            @input="setPhaseTitle"
          />
        </label>
        <label for="Phase_SubmitTitle">
          Submit Title:
          <br />
          <input
            id="Phase_SubmitTitle"
            type="text"
            :value="submitTitle"
            @input="setSubmitTitle"
          />
        </label>
      </props-menu>
      <props-menu title="Widget Selection" :openType="openType">
        <label for="Props_Select">
          Select:
          <br />
          <select
            id="Props_Select"
            multiple="multiple"
            size="3"
            @change="select-answers"
          >
            <option
              v-for="w in getWidgets"
              :value="w.type"
              :key="w.type"
              :selected="isSelectedWidget(w.type)"
            >
              {{ w.type }}
            </option>
          </select>
        </label>
      </props-menu>
      <props-menu title="textfield-answer" :openType="openType">
        <label for="Props_textfield-answer">
          Size (in characters):
          <br />
          <select
            id="Props_textfield-answer"
            :value="size"
            @change="setProp('size', $event.target.value)"
          >
            <option
              v-for="(size, index) in textSizes"
              :value="size"
              :key="index"
            >
              {{ size }}
            </option>
          </select>
        </label>
        <label for="textfield-answer_Answers">
          Answers
          <br />
          <input
            id="textfield-answer_Answers"
            type="text"
            placeholder="dog;cat;parakeet"
            :value="answers"
            @input="setProp('answers', $event.target.value)"
          />
        </label>
        <label for="CFtextfield-answer">
          carry-forward Sources:
          <br />
          <select
            id="CFtextfield-answer"
            multiple
            size="3"
            @change="selectSources($event.target, 'sources')"
          >
            <option
              v-for="src in possibleSources"
              :value="src.wid"
              :key="src.wid"
              :selected="isSelected(src.wid)"
            >
              {{ formatSource(src) }}
            </option>
          </select>
        </label>
        <label for="textfield-answeropt">
          <input
            id="textfield-answeropt"
            type="checkbox"
            :checked="optional"
            @change="setProp('optional', $event.target.checked)"
          />
          Optional
        </label>
      </props-menu>
      <props-menu title="textarea-answer" :openType="openType">
        <label for="CFtextarea-answer">
          carry-forward Sources:
          <br />
          <select
            id="CFtextarea-answer"
            multiple
            size="3"
            @change="selectSources($event.target, 'sources')"
          >
            <option
              v-for="src in possibleSources"
              :value="src.wid"
              :key="src.wid"
              :selected="isSelected(src.wid)"
            >
              {{ formatSource(src) }}
            </option>
          </select>
        </label>
        <label for="textarea-answeropt">
          <input
            id="textarea-answeropt"
            type="checkbox"
            :checked="optional"
            @change="setProp('optional', $event.target.checked)"
          />
          Optional
        </label>
      </props-menu>
      <props-menu title="Select" :openType="openType">
        <label for="Props_Select">
          Options:
          <br />
          <input
            id="Props_Select"
            type="text"
            :value="options"
            @input="setProp('options', $event.target.value)"
            placeholder="dog;cat;parakeet"
          />
        </label>
        <label for="selectsize">
          Size(visible rows):
          <br />
          <input
            id="selectsize"
            type="text"
            :value="size"
            placeholder="4"
            @input="setProp('size', $event.target.value)"
          />
        </label>
        <label for="selectopt">
          <input
            id="selectopt"
            type="checkbox"
            :checked="optional"
            @change="setProp('optional', e.target.checked)"
          />
          Optional
        </label>
      </props-menu>
      <props-menu title="carry-forward" :openType="openType">
        <label for="CF_Src">
          carry-forward Sources:
          <br />
          <select
            id="CF_Src"
            multiple="multiple"
            size="3"
            @change="selectSources($event.target, 'sources')"
          >
            <option
              v-for="src in possibleSources"
              :value="src.wid"
              :key="src.wid"
              :selected="isSelected(src.wid)"
            >
              {{ formatSource(src) }}
            </option>
          </select>
        </label>
      </props-menu>
      <props-menu title="Media" :openType="openType">
        <label for="Media_URL">
          URL:
          <br />
          <input
            id="Media_SRC"
            type="text"
            :value="src"
            placeholder="https://www.arizona.edu"
            @input="setProp('src', $event.target.value)"
          />
        </label>
      </props-menu>
    </div>
    <div v-on:click="toggleTab" id="drawerTab">
      <span :class="{ rotate: isOpen }">
        <img src="@/assets/img/triangle.png" />
      </span>
    </div>
  </div>
</template>

<style lang="scss" scoped>
#propertyWrapper {
  position: relative;
  z-index: 1;
}
.phase-viewer {
  position: absolute;
  width: 170px;
  top: 50px;
  left: -172px;
  padding: 0;
  transition: all 1s ease;
}
#propertyPanel {
  background-color: $bg-color;
}
#drawerTab {
  position: absolute;
  left: 170px;
  border-top: 1px solid $border-color;
  border-right: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  border-radius: 0 4px 4px 0;
  width: 20px;
  height: 20px;
  cursor: pointer;
  top: 50px;
  background-color: $highlight-color;
}
#drawerTab img {
  left: 170px;
  padding: 4px;
  transform: rotate(90deg);
  transition-duration: 1s;
}
.opened {
  left: 0px;
}
#drawerTab .rotate img {
  transform: rotate(-90deg);
  transition-duration: 1s;
}
.propertyPanel {
  position: absolute;
  width: 85%;
  border-radius: 0 8px 8px 0;
  left: 16px;
  top: 0px;
  overflow: auto;
  border: 1px solid $border-color;
  padding: 4px;
  font-size: 12px;
}
.propertyPanel input[type="text"] {
  width: 100px;
}
.propertyPanel select {
  border: 1px solid $border-color;
  width: 100px;
  border-radius: 0;
  overflow: none;
}
</style>
