<script>
import widget-wrapper from "@/views/widgets/widget-wrapper.vue";
import { mapGetters } from "vuex";
export default {
  name: "textarea-answer",
  data: function() {
    return {
      wid: this.$options.wdata.wid
    };
  },
  components: {
    widget-wrapper
  },
  computed: {
    ...mapGetters(["widgetIsLocked", "currentRole"]),
    value() {
      var ans = this.$store.getters["responses/getAnswer"](this.wid);
      if (ans != "") return ans;
      else {
        var sources = this.$store.getters.sources(this.wid);
        if (sources.length > 0)
          return this.$store.getters["responses/combineAnswers"](sources);
        else {
          return "";
        }
      }
    },
    isStudent() {
      return this.currentRole === "student";
    }
  },
  methods: {
    saveAnswer(e) {
      this.$store.commit("responses/saveAnswer", {
        wid: this.wid,
        value: e.target.value
      });
    }
  }
};
</script>
<template>
  <widget-wrapper widgettype="textarea-answer">
    <textarea-answer
      :class="['textarea-answer', { student: isStudent }]"
      @input="saveAnswer($event)"
      :value="value"
      :readonly="widgetIsLocked"
    ></textarea-answer>
  </widget-wrapper>
</template>

<style lang="scss">
.widget textarea-answer {
  font-size: $small-font;
  cursor: grab;
  rows: 4;
  cols: 50;
}
.widget.student textarea-answer {
  cursor: text;
}
</style>
