<script>
import widget-wrapper from "@/views/widgets/widget-wrapper.vue";
//import * as csv from "csvtojson";
export default {
  name: "TableWidget",
  data: function() {
    return {
      wid: this.$options.wdata.wid,
      table: this.$store.getters.table(this.wid)
    };
  },
  components: {
    widget-wrapper
  },
  computed: {
    header() {
      return this.table[0];
    },
    rows() {
      return this.table.length - 1;
    },
    cols(row) {
      return this.table[row];
    }
  },
  methods: {
    dropHandler(e) {
      if (this.currentRole != "student") return;
      var files = [];
      if (e.dataTransfer.items) {
        for (var i = 0; i < e.dataTransfer.items.length; i++) {
          // If dropped items aren't files, reject them
          if (e.dataTransfer.items[i].kind === "file") {
            files.push(e.dataTransfer.items[i].getAsFile());
          }
        }
      }
      if (files.length == 1) {
        var file = files[0];
        if (/^text\/csv/.test(file.type)) this.saveCSV(file);
        else return "Only CSV files are permitted.";
      } else return "Please drop only one file.";
      return "";
    },
    saveCSV() {
      /*
      csv()
        .fromFile(file)
        .then(jsonObj => {
        });
      */
    }
  }
};
