<template>
  <widget-wrapper widgettype="tablewidget">
    <table id="firstTable" v-if="table != null">
      <thead>
        <tr>
          <th v-for="head in header" :key="head.id">
            {{ head.text }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="row in rows" :key="row.id">
          <td v-for="col in cols(row)" :key="col.id">
            {{ col.value }}
          </td>
        </tr>
      </tbody>
    </table>
    <div
      id="tablewidgetdrop"
      v-if="table == null"
      @drop.stop.prevent="dropHandler($event)"
      @dragover.prevent
    >
      <p>Drop your CSV file here</p>
    </div>
  </widget-wrapper>
