<script>
import widget-wrapper from "@/views/widgets/widget-wrapper.vue";
import { mapGetters } from "vuex";
export default {
  name: "carry-forward",
  data: function() {
    return {
      wid: this.$options.wdata.wid
    };
  },
  components: {
    widget-wrapper
  },
  computed: {
    ...mapGetters(["currentRole"]),
    htmlAnswers() {
      let htmlAnswers = [];
      let sources = this.$store.getters["sources"](this.wid);
      Object.values(sources).forEach(wid => {
        let answer = this.$store.getters["responses/getAnswer"](wid);
        let w = this.$store.getters.getWidgetRecord(wid);
        answer =
          w.type === "image-answer"
            ? `<img src="${answer}" />`
            : `<p>${answer}</p>`;
        htmlAnswers.push({ id: wid, answer: answer });
      });
      return htmlAnswers;
    }
  }
};
</script>
<template>
  <widget-wrapper widgettype="carry-forward">
    <div :class="['carry-forward', { student: currentRole == 'student' }]">
      <div
        v-for="html in htmlAnswers"
        :key="html.id"
        v-html="html.answer"
      ></div>
    </div>
  </widget-wrapper>
</template>

<style lang="scss">
.widget[widgettype="carry-forward"] {
  background-color: white;
}
.carry-forward {
  cursor: grab;
  min-width: 100px;
  min-height: 50px;
  resize: both;
  overflow: auto;
  font-size: $txt-font;
  border: 1px dashed $border-color;
}
.carry-forward.student {
  cursor: pointer;
  border: 0;
  resize: none;
  overflow: visible;
}
</style>
