<script>
import event-bus from "@/main";
import phase-bar from "@/views/phase-bar.vue";
import phase-viewer from "@/views/property-drawer.vue";
import phase-viewer from "@/views/phase-viewer.vue";
import submit-bar from "@/views/submit-bar.vue";
import { mapGetters } from "vuex";
export default {
  name: "case-viewer",
  components: {
    phase-bar,
    phase-viewer,
    phase-viewer,
    submit-bar
  },
  data: function() {
    return {
      url: "",
      pdf: null
    };
  },
  mounted() {
    event-bus.$on("loadDocument", info => this.loadDocument(info));
  },
  methods: {
    loadDocument(info) {
      this.url = info.url;
      import("pdfjs-dist/webpack").then(pdfjs => {
        this.$store.commit("setDefaultState");
        let loadingTask = pdfjs.getDocument(info.url);
        loadingTask.promise.then(pdf => {
          this.pdf = pdf;
          info.setState(pdf, this.url);
        });
      });
    }
  },
  computed: {
    ...mapGetters(["currentRole", "currentPhase", "getPhases"]),
    getPDF() {
      return this.pdf;
    },
    hasPhases() {
      return this.getPhases.length > 0 && this.currentPhase > -1;
    }
  }
};
</script>
<template>
  <div id="case-viewer">
    <phase-bar></phase-bar>
    <div id="phase-container">
      <phase-viewer
        v-for="(phase, index) in getPhases"
        :pdf="getPDF"
        :pindex="index"
        :scale="1.0"
        :key="phase.id"
      />
    </div>
    <phase-viewer v-if="currentRole == 'designer' && hasPhases">
    </phase-viewer>
    <submit-bar></submit-bar>
  </div>
</template>
}
<style lang="scss" scoped>
@import "../../node_modules/pdfjs-dist/web/pdf_viewer.css";
#case-viewer {
  position: relative;
  flex: 2 1 auto;
  height: 100%;
  min-height: 350px;
  min-width: 70%;
  border-right: 1px solid $border-color;
}
#phase-container {
  position: relative;
  height: 100%;
}
</style>
