<script>
import widget-wrapper from "@/views/widgets/widget-wrapper.vue";
export default {
  name: "select-answer",
  data: function() {
    return {
      wid: this.$options.wdata.wid
    };
  },
  components: {
    widget-wrapper
  },
  computed: {
    options() {
      return this.$store.getters.options(this.wid).split(";");
    },
    size() {
      return this.$store.getters.size(this.wid);
    }
  },
  methods: {
    saveAnswer(e) {
      this.$store.commit("responses/saveAnswer", {
        wid: this.wid,
        value: e.target.value
      });
    }
  }
};
</script>
<template>
  <widget-wrapper widgettype="select">
    <select class="select" :size="size" @change="saveAnswer($event)">
      <option v-for="option in options" :key="option" :value="option">
        {{ option }}
      </option>
    </select>
  </widget-wrapper>
</template>

<style lang="scss">
.widget[widgettype="select"] select {
  min-width: 50px;
  border-radius: 0;
  cursor: default;
}
</style>
